import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';

import '../styles/dashboard.css';
import '../styles/global.css';

import red from '@material-ui/core/colors/red';
import blue from '@material-ui/core/colors/blue';
import green from '@material-ui/core/colors/green';
import Radio from '@material-ui/core/Radio';
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import { withStyles } from '@material-ui/core/styles';

class InputTextField extends Component {
    constructor(props) {
        super(props);

        this.state = {
            text: '',
            sent: "0"
        }
    }

    handleChange = (e) => {
        this.setState({ sent: e.target.value });
    };


    getText = () => {
        return this.state.text;
    }

    getSent = () => { return this.state.sent }

    handleChanged = (event) => {
        this.setState({
            text: event.target.value
        });
    }

    render() {
        const { classes } = this.props;
        return (
            <div style={{display: 'flex', flexDirection: 'row'}}>
                <TextField
                multiline={this.props.longform}
                fullWidth={this.props.longform}
                onChange={this.handleChanged}
                label="text" />
                        {this.props.sents && <div>
                        <Radio
                            checked={this.state.sent === "0"}
                            onChange={e=>this.handleChange(e)}
                            value="0"
                            name="radio-button-demo"
                            aria-label="A"
                            style={{
                                color: blue[600],
                                '&$checked': {
                                    color: blue[500],
                                },
                            }}
                        />
                        <Radio
                            checked={this.state.sent === "1"}
                            onChange={e=>this.handleChange(e)}
                            value="1"
                            name="radio-button-demo"
                            aria-label="B"
                            style={{
                                color: green[600],
                                '&$checked': {
                                    color: green[500],
                                },
                            }}
                        />
                        <Radio
                            checked={this.state.sent === "2"}
                            onChange={e=>this.handleChange(e)}
                            value="2"
                            name="radio-button-demo"
                            aria-label="C"
                            style={{
                                color: red[600],
                                '&$checked': {
                                    color: red[500],
                                },
                            }}
                        />
                        </div>}
                    </div>
            
        );
    }
}

export default InputTextField;
